import React from 'react';
import api from '../../../Global/Api';
import 'antd/dist/antd.css';
import {Row, Col, Card} from 'react-bootstrap';

import Aux from "../../../../hoc/_Aux";
import { Form,Input,Button,Modal,Checkbox} from 'antd';

class RegistrationForm extends React.Component {
  constructor(props){
    super(props);
    this.initialState = {
      id:'',
      name:'',
      permissions:[],
      permissiondata:[],
      group:[],
      GroupId : this.props.match.params.GroupId,
      checkedList:[],
      indeterminate: true,
      checkAll: false,
   
    };
    
    
    if(this.group){
      this.state =this.group
    } else {
      this.state = this.initialState;
    }

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  success() {
    Modal.success({
      content: 'Successively Updated',
    });
  }
  
  error() {
    Modal.error({
      title: 'This is an error message',
      content: 'Error Occurred!!',
    });
  }

  componentDidMount() {
    this.fetch();
    this.fetchpermissions();
  }

  fetch = () => {
  this.setState({ loading: true });
  api.get(`/api/group/${this.state.GroupId}`,{}).then(res => {
      this.setState({
        loading: false,
        group: res.data,
        checkedList: res.data.permissions,
      });
    });
};

  fetchpermissions = () => {
    this.setState({ loading: true });
    api.get(`/api/permission`,{}).then(res => {
        this.setState({
          loading: false,
          permissiondata: res.data,
        });
      });
  };
  
  onChange = checkedList => {
    this.setState({
      checkedList,
    });
  };


  handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, value) => {
        if (!err) {
          this.setState({loading:true});
          if(this.props.match.params.GroupId){
          api.put(`/api/group/${this.state.GroupId}`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 200){
                  this.success();
              }
            })
            .catch(error => {
              this.error();
            });
          }
          else{
              api.post(`/api/group`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 201){
                  this.success();
              }
            })
            .catch(error => {
              this.error();
            });
            }
         }
      });
    };


    render() {
      
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
      
      const permissionsvalue = this.state.permissiondata.map((permission) =>
      <Col md={6}>
        <Checkbox value={permission.id}>{permission.name}</Checkbox>
      </Col>
      );
      return (
        <Aux>
        <Row>
            <Col>
                <Card>
                    <Card.Header>
                        <Card.Title as="h5">Group Form</Card.Title>
                    </Card.Header>
                    <Card.Body>
                        <Row>
                            <Col md={9}>
                              <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                <Form.Item label="Group Name">
                                  {getFieldDecorator('name', { initialValue: this.state.group.name,
                                    rules: [
                                      {
                                        required: true,
                                      },
                                    ],
                                  })(<Input />)}
                                </Form.Item>
                                <Form.Item {...tailFormItemLayout}>
                                {getFieldDecorator('permissions', {
                                  valuePropName: 'checked',
                                })(
                                  <Checkbox.Group style={{ width: '100%' }} value={this.state.checkedList}  onChange={this.onChange}>
                                        <Row>
                                         {permissionsvalue}
                                        </Row> 
                                 </Checkbox.Group>,
                                  )}
                                </Form.Item>
                              
                                 <Form.Item {...tailFormItemLayout}>
                                  <Button type="primary" htmlType="submit">
                                    Submit
                                  </Button>
                                </Form.Item>
                              </Form>
                              </Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
      );
    }
  }
  const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
   
  export default WrappedRegistrationForm;
